/* EventList.css */

/* Common styles for buttons */
.button {
  width: 100%; /* Adjusted width to accommodate divider */
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  margin: 0 auto; /* Center the button horizontally */
  margin-bottom: 3px;
  border: none;
  background-color: var(--tg-theme-section-bg-color);
  color: var(--tg-theme-text-color);
  cursor: pointer;
  position: relative; /* Add relative positioning for ripple effect */
  overflow: hidden; /* Ensure ripple doesn't overflow */

  /* Remove box-shadow and border-radius */
  box-shadow: none;
  border-radius: 33%;

  /* If you want to remove button hover effects */
  /* transition: none; */
}



/* Ripple effect */
.ripple {
  position: absolute;
  background-color: rgba(0, 102, 255, 0.4);
  border-radius: 50%;
  transform: scale(0);
  animation: rippleEffect 0.7s linear;
}

@keyframes rippleEffect {
  to {
    transform: scale(5);
    opacity: 0;
  }
}


.event-list-button {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.event-name,
.event-details,
.closing-info {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.event-name {
  font-weight: bold;
}

.spinner{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999; /* Set a higher z-index to make sure it's above other elements */
}






