/* EventDetails.css */

.event-details-overlay {
  display: flex;
  justify-content: center;
  align-items: flex-end; /* Align items to the bottom */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 988;
  overflow: hidden; /* Hide overflow to prevent content from showing outside the modal */
  animation: slideIn 0.3s forwards; /* Apply animation for sliding in */
}

@keyframes slideIn {
  from {
    transform: translateY(100vh); /* Start from below the viewport */
  }
  to {
    transform: translateY(0); /* Slide to the center */
  }
}

.event-details-content {
  position: relative;
  width: 70vw;
  background-color: rgba(255, 255, 255, 0.9);
  border: 2px solid rgb(167, 195, 255);
  padding: 0;
  margin: auto; /* Center horizontally */
  border-radius: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  animation: slideContent 0.3s forwards; /* Apply animation for sliding in content */
}

@keyframes slideContent {
  from {
    opacity: 0; /* Start with content hidden */
    transform: translateY(50px); /* Slide content from bottom */
  }
  to {
    opacity: 1; /* Show content */
    transform: translateY(0); /* Slide content to its position */
  }
}
/* -------------------------------------------------------------------------- */

/* Animation for sliding out the overlay */
.event-details-overlay.closing {
  animation: slideOutOverlay 0.5s forwards; /* Apply animation for sliding out */
}

@keyframes slideOutOverlay {
  from {
    opacity: 1; /* Start with the overlay fully visible */
  }
  to {
    opacity: 0; /* Fade out the overlay */
  }
}

/* Animation for sliding out the content */
.event-details-content.closing {
  animation: slideOutContent 0.5s forwards; /* Apply animation for sliding out */
}

@keyframes slideOutContent {
  from {
    transform: translateY(0); /* Start from the center */
    opacity: 1; /* Start fully visible */
  }
  to {
    transform: translateY(-100%); /* Slide up above the viewport */
    opacity: 0; /* Fade out */
  }
}

/* -------------------------------------------------------------------------- */
.modul-event-name {
  text-align: center;
  align-items: center;
  padding-top: 15px;
  margin: 0;
  padding-bottom: 0;
}
.p-info {
  text-align: left; /* Align text to the left */
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 0;
  padding-bottom: 0;
  margin: 0;
}

.close-button {
  position: absolute;
  top: -20px; /* Position the button above the modal window */
  right: -20px; /* Position the button outside the modal window */
  width: 35px; /* Adjust the width and height to make the button circular */
  height: 35px;
  border-radius: 50%; /* Make the button circular */
  background-color: rgba(170, 170, 170, 0.5); /* Semi-transparent background color */
  border: 2px solid rgb(167, 195, 255); /* Blue border with 2px size */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px; /* Adjust font size as needed */
  color: rgb(167, 195, 255); /* Text color */
  cursor: pointer;
  z-index: 999; /* Ensure the button is above other elements */
}

.close-button:hover {
  color: white; /* Text color on hover */
  border-color: white; /* Border color on hover */
}

.spinner {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999; /* Set a higher z-index to make sure it's above other elements */
}
